import React from "react";
import { useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import "./css/header_nav.css";
import logo from "../assets/images/logo.png"

function show_or_hide() {
  let element = document.getElementById("nav-linkss");
  if (element.style.display === "none") {
    element.style.display = "block";
  } else {
    element.style.display = "none";
  }
}

export default function Header_nav() {
  const location = useLocation();

  useEffect(() => {
    if (location.pathname === "/blog") {
      document.title = "Bistro Bliss - Blog";
    } else if (location.pathname === "/about") {
      document.title = "Bistro Bliss - About Us";
    } else if (location.pathname === "/Menu") {
      document.title = "Bistro Bliss - Menu";
    } else if (location.pathname === "/contact") {
      document.title = "Bistro Bliss - Contact";
    } else if (location.pathname === "/cart") {
      document.title = "Bistro Bliss - Cart";
    } else if (location.pathname === "/blogs_details") {
      document.title = "Bistro Bliss - Blogs Details";
    } 
    else {
      document.title = "Bistro Bliss";
    }
  }, [location]);

  return (
    <frameElement>
      <header>
        <div className="home-header-info">
          <p>
            <i className="fa-solid fa-phone"></i> (414) 857 - 0107
          </p>
          <p>
            <i className="fa-solid fa-envelope"></i> yummy@bistrobliss
          </p>
        </div>
        <div className="home-header-links">
          <Link to="https://www.twitter.com" target="_blank">
            <i className="fab fa-twitter"></i>
          </Link>
          <Link to="https://www.facebook.com" target="_blank">
            <i className="fab fa-facebook"></i>
          </Link>
          <Link to="https://www.instagram.com" target="_blank">
            <i className="fab fa-instagram"></i>
          </Link>
          <Link to="https://www.github.com" target="_blank">
            <i className="fa-brands fa-github"></i>
          </Link>
        </div>
      </header>
      <nav>
        <div className="nav-logo">
          <img src={logo} alt="" />
          <h1 className="nav-h1">Bistro Bliss</h1>
        </div>
        <div id="nav-linkss">
          <Link to="/" className={location.pathname === "/" ? "selected" : ""}>
            Home
          </Link>
          <Link
            to="/about"
            className={location.pathname === "/about" ? "selected" : ""}
          >
            About
          </Link>
          <Link
            to="/Menu"
            className={location.pathname === "/Menu" ? "selected" : ""}
          >
            Menu
          </Link>
          <Link
            to="/blog"
            className={
              ["/blog", "/blogs_details"].includes(location.pathname)
                ? "selected"
                : ""
            }
          >
            Pages
          </Link>
          <Link
            to="/contact"
            className={location.pathname === "/contact" ? "selected" : ""}
          >
            Contact
          </Link>
          <Link
            to="/cart"
            className={location.pathname === "/cart" ? "selected" : ""}
          >
            <i className="fas fa-shopping-cart"></i>
          </Link>
          <Link className="book-table" to="/bookatable">
            Book A Table
          </Link>
        </div>
        <button id="nav-bars" onClick={show_or_hide}>
          <i className="fa-solid fa-bars"></i>
        </button>
      </nav>
    </frameElement>
  );
}
