import React from "react";
import '../css/about.css'
import user1 from "../../assets/images/about-imgs/user1.jpg"
import user2 from "../../assets/images/about-imgs/user2.jpg"
import user3 from "../../assets/images/about-imgs/user3.jpg"

const CustomerCard1 = () => {
    return (
        <div className="sec4-card">
        <h3>“The best restaurant”</h3>
        <p>
            Last night, we dined at place and were simply blown away. From the moment we stepped in, we were enveloped in an inviting atmosphere and greeted with warm smiles.
        </p>
        <div className="line-sec4"></div>
        <div className="user-sec4">
            <img src={user1} alt="user1" />
            <h5>Sophire Robson</h5>
            <p>Los Angeles, CA</p>
        </div>
        </div>
    );
};

const CustomerCard2 = () => {
    return (
        <div className="sec4-card">
        <h3>“Simply delicious”</h3>
        <p>
            Place exceeded my expectations on all fronts. The ambiance was cozy and relaxed, making it a perfect venue for our anniversary dinner. Each dish was beautifully presented.
        </p>
        <div className="line-sec4"></div>
        <div className="user-sec4">
            <img src={user2} alt="user2" />
            <h5>Matt Cannon</h5>
            <p>San Diego, CA</p>
        </div>
        </div>
    );
    };

const CustomerCard3 = () => {
    return (
        <div className="sec4-card">
        <h3>“One of a kind restaurant”</h3>
        <p>
            The culinary experience at place is first to none. The atmosphere is vibrant, the food - nothing short of extraordinary. Highly recommended.
        </p>
        <div className="line-sec4" ></div>
        <div className="user-sec4" >
            <img src={user3} alt="user3" />
            <h5>Andy Smith</h5>
            <p>San Francisco, CA</p>
        </div>
        </div>
    );
};

const AboutSec4 = () => {
    return (
        <div className="sec4">
        <h2>What Our Customers Say</h2>
        <div className="sec4-content">
            <CustomerCard1 />
            <CustomerCard2 />
            <CustomerCard3 />
        </div>
        </div>
    );
};

export default AboutSec4;
