import React from "react";
import "./css/checkout.css";

function Checkout() {

    console.log("Toggle nav clicked");
   
  return (
    <div>

      {/* Hero */}
      <section className="checkout-hero">
        <h1>🧾 Checkout</h1>
        <p>Please fill in your details to complete your order.</p>
      </section>

      {/* Form */}
      <section className="checkout-form-container">
        <form className="checkout-form">
          <div>
            <label htmlFor="fullname">Full Name</label>
            <input type="text" id="fullname" placeholder="Your Name" required />
          </div>
          <div>
            <label htmlFor="email">Email</label>
            <input type="email" id="email" placeholder="you@example.com" required />
          </div>
          <div>
            <label htmlFor="phone">Phone</label>
            <input type="tel" id="phone" placeholder="0123-456-789" required />
          </div>
          <div>
            <label htmlFor="address">Address</label>
            <input type="text" id="address" placeholder="Street Address" required />
          </div>
          <div>
            <label htmlFor="city">City</label>
            <input type="text" id="city" placeholder="Your City" required />
          </div>
          <div>
            <label htmlFor="payment">Payment Method</label>
            <select id="payment" required>
              <option value="">Select</option>
              <option>Cash on Delivery</option>
              <option>Credit Card</option>
              <option>PayPal</option>
            </select>
          </div>
          <div className="checkout-button">
            <button type="submit">Place Order</button>
          </div>
        </form>
      </section>

    </div>
  );
}

export default Checkout;
