import React from "react";
import '../css/about.css'
import maskimage from "../../assets/images/about-imgs/Mask group (1).jpg"

const FactBox = ({ number, label }) => {
    return (
        <div className="facts-box">
        <div className="number">{number}</div>
        <p>{label}</p>
        </div>
    );
};

const FactsSection = () => {
    return (
        <div className="facts">
        <FactBox number="3" label="Locations" />
        <FactBox number="1995" label="Founded" />
        <FactBox number="65+" label="Staff Members" />
        <FactBox number="100%" label="Satisfied Customers" />
        </div>
    );
};

const LeftContent = () => {
    return (
        <div className="sec3-left-content">
        <div className="title-info">
            <h2>A little information for our valuable guest</h2>
            <p>
            At place, we believe that dining is not just about food, but also
            about the overall experience. Our staff, renowned for their warmth
            and dedication, strives to make every visit an unforgettable event.
            </p>
        </div>
        <FactsSection />
        </div>
    );
};

const ImageSection = () => {
    return (
        <div className="mask-img">
        <img src={maskimage} alt="mask group" />
        </div>
    );
};

const AboutSec3 = () => {
    return (
        <div className="sec3-about">
        <LeftContent />
        <ImageSection />
        </div>
    );
};

export default AboutSec3;
