import React from 'react';
import '../HomeCss/Sec4.css';
import cateringsimage from "../../assets/Home-imgs/images sec4/3.jpg"
import birthdaysimage from "../../assets/Home-imgs/images sec4/1.jpg"
import widdingsimage from "../../assets/Home-imgs/images sec4/7.jpg"
import eventsimage from "../../assets/Home-imgs/images sec4/6.jpg"


function Sec4() {
  return (
    <section className="services-section">
      <h2>We also offer unique<br />services for your events</h2>

      <div className="services-container">
        <div className="service-card">
          <img src={cateringsimage} alt="Caterings" />
          <h3>Caterings</h3>
          <p>In the new era of technology we look in the future with certainty for life.</p>
        </div>

        <div className="service-card">
          <img src={birthdaysimage} alt="Birthdays" />
          <h3>Birthdays</h3>
          <p>In the new era of technology we look in the future with certainty for life.</p>
        </div>

        <div className="service-card">
          <img src={widdingsimage} alt="Weddings" />
          <h3>Weddings</h3>
          <p>In the new era of technology we look in the future with certainty for life.</p>
        </div>

        <div className="service-card">
          <img src={eventsimage} alt="Events" />
          <h3>Events</h3>
          <p>In the new era of technology we look in the future with certainty for life.</p>
        </div>
      </div>
    </section>
  );
}

export default Sec4;
