import React from "react";
import styles from "../css/BlogsDetails.module.css";
import ArticleCard from "./ArticleCard";

import article1 from "../../assets/BlogDetails/article1.png";
import article2 from "../../assets/BlogDetails/article2.png";
import article3 from "../../assets/BlogDetails/article3.png";
import article4 from "../../assets/BlogDetails/article4.png";

const ArticleSection = () => {
  const articles = [
    {
      image: article1,
      alt: "sushi",
      title: "How to prepare a delicious gluten free sushi",
      date: "January 3, 2023"
    },
    {
      image: article2,
      alt: "baking",
      title: "Exclusive baking lessons from the pastry king",
      date: "January 3, 2023"
    },
    {
      image: article3,
      alt: "fries",
      title: "How to prepare the perfect fries in an air fryer",
      date: "January 3, 2023"
    },
    {
      image: article4,
      alt: "chicken",
      title: "How to prepare delicious chicken tenders",
      date: "January 3, 2023"
    }
  ];

  return (
    <section className={styles.articles}>
      <h2>Read More Articles</h2>
      <div className={styles.articlesInfo}>
        <p className={styles.pArticles}>
          We consider all the drivers of change gives you the components you need</p>
        <p className={styles.pArticles}>to change to create a truly happens</p>
      </div>

      <div className={styles.cardContainer}>
        {articles.map((article, index) => (
          <ArticleCard
            key={index}
            image={article.image}
            alt={article.alt}
            title={article.title}
            date={article.date}
          />
        ))}
      </div>

      <button
        className={styles.backBtn}
        onClick={() => window.location.href = "/blog"}>⟵ Main Page</button>
    </section>
  );
};

export default ArticleSection;