import React from "react";
import "./css/Book.css";
import mapimage from "../assets/images/book/map.png"

function Book() {
  return (
    <div className="book-page">
      
      {/* Hero Section */}
      <section className="book-hero">
        <h1>Book A Table</h1>
        <p>
          We consider all the drivers of change, giving you the components you need to create real transformation.
        </p>
      </section>

      {/* Booking Form */}
      <section className="form-container">
        <form className="booking-form">
          <div className="form-group">
            <label htmlFor="date">Date</label>
            <input type="date" id="date" required />
          </div>
          <div className="form-group">
            <label htmlFor="time">Time</label>
            <input type="time" id="time" required />
          </div>
          <div className="form-group">
            <label htmlFor="name">Name</label>
            <input type="text" id="name" placeholder="Your Name" required />
          </div>
          <div className="form-group">
            <label htmlFor="phone">Phone</label>
            <input type="tel" id="phone" placeholder="Phone Number" required />
          </div>
          <div className="form-group full-row">
            <label htmlFor="guests">Total Person</label>
            <select id="guests" required>
              <option value="">Select</option>
              <option>1</option>
              <option>2</option>
              <option>3</option>
              <option>4+</option>
            </select>
          </div>
          <div className="form-group checkout-button">
            <button type="submit" 
            onClick={() => window.location.href = "/confirmation"}>Book A Table</button>
          </div>
        </form>
      </section>

      {/* Map Section */}
      <section className="map-section">
        <img src={mapimage} alt="Map" className="map-image" />
      </section>
    </div>
  );
}

export default Book;
