import React from "react";
import '../HomeCss/Sec2&3.css';
import breakfastimage from "../../assets/Home-imgs/sec2&3 imgs/icon.jpg"
import maindishesimage from "../../assets/Home-imgs/sec2&3 imgs/icon2.jpg"
import drinksimage from "../../assets/Home-imgs/sec2&3 imgs/icon3.jpg"
import dessertsimage from "../../assets/Home-imgs/sec2&3 imgs/icon4.jpg"



const ExploreButton = () => {
    return <button className="explore-btn">Explore Menu</button>;
};

const Browse = ({ image, title, description }) => {
    return (
        <div className="card">
        <img src={image} alt={title} />
        <h4>{title}</h4>
        <p>{description}</p>
        <ExploreButton />
        </div>
    );
    };
const Sec2Home = () => {
    const menuItems = [
        {
        image: breakfastimage,
        title: "Breakfast",
        description:
            "In the new era of technology we look in the future with certainty and pride for our life.",
        },
        {
        image: maindishesimage ,
        title: "Main Dishes",
        description:
            "In the new era of technology we look in the future with certainty and pride for our life.",
        },
        {
        image: drinksimage,
        title: "Drinks",
        description:
            "In the new era of technology we look in the future with certainty and pride for our life.",
        },
        {
        image: dessertsimage,
        title: "Desserts",
        description:
            "In the new era of technology we look in the future with certainty and pride for our life.",
        },
    ];

    return (
        <div className="sec2-homePage">
        <h2 className="sec2-h">Browse Our Menu</h2>
        <div className="cards-container">
            {menuItems.map((item, index) => (
            <Browse
                key={index}
                image={item.image}
                title={item.title}
                description={item.description}
            />
            ))}
        </div>
        </div>
    );
    };

export default Sec2Home;
