import React from 'react';
// import styles from "../pages/HomeSixSeven.module.css";
import styles from "../HomeCss/HomeSixSeven.module.css";

import review1 from '../../assets/Section6&7/review1.png';
import review2 from '../../assets/Section6&7/review2.png';
import review3 from '../../assets/Section6&7/review3.png';


const testimonials = [
  {
    title: "“The best restaurant”",
    text: "Last night, we dined at place and were simply blown away. From the moment we stepped in, we were enveloped in an inviting atmosphere and greeted with warm smiles.",
    name: "Sophire Robson",
    location: "Los Angeles, CA",
    img: review1,
  },
  {
    title: "“Simply delicious”",
    text: "Place exceeded my expectations on all fronts. The ambiance was cozy and relaxed, making it a perfect venue for our anniversary dinner. Each dish was prepared and beautifully presented.",
    name: "Matt Cannon",
    location: "San Diego, CA",
    img: review2,
  },
  {
    title: "“One of a kind restaurant”",
    text: "The culinary experience at place is first to none. The atmosphere is vibrant, the food - nothing short of extraordinary. The food was the highlight of our evening. Highly recommended.",
    name: "Andy Smith",
    location: "San Francisco, CA",
    img: review3,
  },
];

const Testimonials = () => (
  <section className={styles.testimonial}>
    <h2 className={styles.titles}>What Our Customers Say</h2>
    <div className={styles.reviewsHolder}>
      {testimonials.map((review, index) => (
        <div className={styles.reviews} key={index}>
          <h3>{review.title}</h3>
          <p>{review.text}</p>
          <hr />
          <div className={styles.reviewsInfo}>
            <div>
              <img src={review.img} alt={review.name} />
            </div>
            <div>
              <h5>{review.name}</h5>
              <p>{review.location}</p>
            </div>
          </div>
        </div>
      ))}
    </div>
  </section>
);

export default Testimonials;