import React, { useEffect, useState } from "react";
import "./css/cart.css";
import toastr from "toastr";
import "toastr/build/toastr.min.css";

function CartPage() {
  const [cartItems, setCartItems] = useState([]);
  const [subtotal, setSubtotal] = useState(0);

  useEffect(() => {
    renderCart();
  }, []);

  const renderCart = () => {
    const items = JSON.parse(localStorage.getItem("cart")) || [];
    setCartItems(items);
    const total = items.reduce(
      (sum, item) => sum + item.price * item.quantity,
      0
    );
    setSubtotal(total);
  };

  const changeQuantity = (index, updatedIndex) => {
    const updatedCart = [...cartItems];
    updatedCart[index].quantity =
      Number(updatedCart[index].quantity) + updatedIndex;
    if (updatedCart[index].quantity <= 0) {
      updatedCart.splice(index, 1);
    }

    localStorage.setItem("cart", JSON.stringify(updatedCart));
    setCartItems(updatedCart);
    renderCart();
  };

  const removeItem = (index) => {
    const updatedCart = [...cartItems];
    updatedCart.splice(index, 1);
    localStorage.setItem("cart", JSON.stringify(updatedCart));
    setCartItems(updatedCart);
    renderCart();
  };

  const checkout = () => {
    toastr.success("Your Order placed Successfully!");
    localStorage.removeItem("cart");
    setCartItems([]);
    setSubtotal(0);
  };

  return (
    <>
      <main>
        <div className="cart-page">
          <h2>Your Cart</h2>
          <div className="cart-page-container">
            <div id="cart-items" className="cart-list">
              {cartItems.length === 0 ? (
                <p className="empty-cart">Your cart is empty!</p>
              ) : (
                cartItems.map((item, index) => (
                  <div key={index} className="cart-item">
                    <img src={item.image} width="80" alt={item.title} />
                    <div>
                      <h3>{item.title}</h3>
                      <p className="cart-items-price">${item.price}</p>
                      <div className="cart-items-controls-section">
                        <button
                          onClick={() => changeQuantity(index, -1)}
                          className="cart-items-control"
                        >
                          -
                        </button>
                        <span>{item.quantity}</span>
                        <button
                          onClick={() => changeQuantity(index, 1)}
                          className="cart-items-control"
                        >
                          +
                        </button>
                        <button
                          onClick={() => removeItem(index)}
                          className="cart-items-remove-btn"
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>

            <div className="total-box">
              <h3>Order Summary</h3>
              <div className="total-details">
                <p>
                  Subtotal: $<span id="subtotal">{subtotal}</span>
                </p>
                <p>Shipping: Free</p>
                <hr />
                <p>
                  <strong>
                    Total: $<span id="total">{subtotal}</span>
                  </strong>
                </p>
              </div>
              <button
                className="checkout-btn"
                onClick={() => {
                  checkout();
                  window.location.href = "/Checkout";
                }}
              >
                Checkout
              </button>
            </div>
          </div>
        </div>
      </main>
    </>
  );
}

export default CartPage;
