import React from "react";
import { Link } from "react-router-dom";
import "./css/footer.css";
import image1 from "../assets/images/footer/img-1.png"
import image2 from "../assets/images/footer/img-2.png"
import image3 from "../assets/images/footer/img-3.png"
import image4 from "../assets/images/footer/img-4.png"
import logo from "../assets/images/footer/logo.svg"
const Footer = () => {
  return (
    <>
      <footer className="footer">
        <div className="footer-container">
          <section className="footer-logo-section">
            <p className="footer-logo">
              <img src={logo} alt="footer-logo" />
              Bistro Bliss
            </p>
            <p className="footer-logo-paragraph">
              In the new era of technology we look to the future with certainty
              and pride for our company.
            </p>
            <div className="footer-social-icons">
              <a
                href="https://www.twitter.com"
                target="_blank"
                rel="noreferrer"
              >
                <i className="fab fa-twitter"></i>
              </a>
              <a
                href="https://www.facebook.com"
                target="_blank"
                rel="noreferrer"
              >
                <i className="fab fa-facebook"></i>
              </a>
              <a
                href="https://www.instagram.com"
                target="_blank"
                rel="noreferrer"
              >
                <i className="fab fa-instagram"></i>
              </a>
              <a href="https://www.github.com" target="_blank" rel="noreferrer">
                <i className="fab fa-github"></i>
              </a>
            </div>
          </section>

          <section className="footer-links-section">
            <p>Pages</p>
            <ul>
              <li>
                <Link to ="/">Home</Link>
              </li>
              <li>
                <Link to ="/about.jsx">About</Link>
              </li>
              <li>
                <Link to ="/menu.jsx">Menu</Link>
              </li>
              <li>
                <Link to ="/">Pricing</Link>
              </li>
              <li>
                <Link to ="/blog.jsx">Blog</Link>
              </li>
              <li>
                <Link to ="/contact.jsx">Contact</Link>
              </li>
              <li>
                <Link to ="/">Delivery</Link>
              </li>
            </ul>
          </section>

          <section className="footer-links-section">
            <p>Utility Pages</p>
            <ul>
              <li>
                <Link to="/">Start Here</Link>
              </li>
              <li>
                <Link to="/">Styleguide</Link>
              </li>
              <li>
                <Link to="/">404 Not Found</Link>
              </li>
              <li>
                <Link to="/">Changelog</Link>
              </li>
            </ul>
          </section>

          <section className="footer-img-section">
            <p>Follow Us On Instagram</p>
            <div className="footer-img">
              <img src={image1} alt="" />
              <img src={image2} alt="" />
              <img src={image3} alt="" />
              <img src={image4} alt="" />
            </div>
          </section>
        </div>
        <section className="footer-bottom col-s-12">
          <hr className="footer-line" />
          <p>Copyright © 2023 Hashtag Developer. All Rights Reserved.</p>
        </section>
      </footer>
    </>
  );
};
export default Footer;
