import React, { useState } from "react";
import "./css/contact.css";
import toastr from "toastr";
import 'toastr/build/toastr.min.css';

function ContactPage() {
  const [formData, setFormData] = useState({
    userName: "",
    userEmail: "",
    userSubject: "",
    userMessage: "",
  });

  const newData = (send) => {
    setFormData((oldData) => ({
      ...oldData,
      [send.target.name]: send.target.value,
    }));
  };

  const submitData = (send) => {
    send.preventDefault();
    const messages = JSON.parse(localStorage.getItem("contactMessages")) || [];
    messages.push({
      ...formData,
      Date: new Date().toLocaleString(),
    });
    localStorage.setItem("contactMessages", JSON.stringify(messages));
    toastr.success("Your message has been sent successfully!");
    setFormData({
      userName: "",
      userEmail: "",
      userSubject: "",
      userMessage: "",
    });
  };

  return (
    <>
      <main>
        <section className="contact-hero-section">
          <h2>Contact Us</h2>
          <p>
            We consider all the drivers of change gives you the components you
            need to change to create a truly happens.
          </p>
        </section>

        <form className="contact-form" onSubmit={submitData}>
          <div className="contact-row">
            <div className="contact-name-label">
              <label>Name</label>
              <input
                type="text"
                name="userName"
                className="contact-user-data"
                placeholder="Enter your name"
                required
                value={formData.userName}
                onChange={newData}
              />
            </div>
            <div className="contact-email-label">
              <label>Email</label>
              <input
                type="email"
                name="userEmail"
                className="contact-user-data"
                placeholder="Enter your email"
                required
                value={formData.userEmail}
                onChange={newData}
              />
            </div>
          </div>
          <label>Subject</label>
          <input
            type="text"
            name="userSubject"
            className="contact-text-data"
            placeholder="Write a subject"
            required
            value={formData.userSubject}
            onChange={newData}
          />
          <label>Message</label>
          <textarea
            name="userMessage"
            className="contact-text-data"
            id="contactUserMessage"
            placeholder="Write your message"
            required
            value={formData.userMessage}
            onChange={newData}
          ></textarea>
          <button className="contact-submit-btn">Send</button>
        </form>

        <section className="contact-info-section">
          <article>
            <h4>Call Us:</h4>
            <p className="contact-phone">+1-234-567-8900</p>
          </article>
          <article>
            <h4>Hours:</h4>
            <p className="contact-info-detailes">Mon-Fri: 11am - 8pm</p>
            <p className="contact-info-detailes">Sat, Sun: 9am - 10pm</p>
          </article>
          <article>
            <h4>Our Location:</h4>
            <p className="contact-info-detailes">123 Bridge Street</p>
            <p className="contact-info-detailes">Nowhere Land, LA 12345</p>
            <p className="contact-info-detailes">United States</p>
          </article>
        </section>
      </main>
    </>
  );
};

export default ContactPage;
