import React from "react";
import '../css/about.css'
import aboutimage from "../../assets/images/about-imgs/about1.jpg"
const AboutImage = () => {
    return (
        <img 
        className="about-image"
        src={aboutimage}
        alt="about1-img"
        />
    );
};

const AboutContactBox = () => {
    return (
        <div className="aboutContact-box">
        <h4>Come and visit us</h4>
        <div className="aboutbox-info">
            <p><i className="fa-solid fa-phone"></i> (414) 857 - 0107</p>
            <p><i className="fa-solid fa-envelope"></i> happytummy@restaurant.com</p>
            <p><i className="fa-solid fa-location-dot"></i> 837 W. Marshall Lane Marshalltown, IA 50158, Los Angeles</p>
        </div>
        </div>
    );
};

const AboutTextBox = () => {
    return (
        <div className="about-rightText">
        <h2>We provide healthy food for your family.</h2>
        <p className="first-paragraph">Our story began with a vision to create a unique dining experience that merges fine dining, exceptional service, and a vibrant ambiance. Rooted in city's rich culinary culture, we aim to honor our local roots while infusing a global palate.</p>
        <p className="sec-paragraph">At place, we believe that dining is not just about food, but also about the overall experience. Our staff, renowned for their warmth and dedication, strives to make every visit an unforgettable event.</p>
        </div>
    );
};

const AboutIntro = () => {
    return (
        <div className="about-contaiener">
        <AboutImage />
        <AboutContactBox />
        <AboutTextBox />
    </div>
    );
};
export default AboutIntro;