import React from "react";
import '../css/about.css'
import sec2image from "../../assets/images/about-imgs/sec2.jpg"
import res_menuimage from "../../assets/images/about-imgs/restaurant-menu 1.jpg"
import Aicon from "../../assets/images/about-imgs/Aicon.jpg"
import timerimage from "../../assets/images/about-imgs/timer 1.jpg"

const FeatureBox = ({ image, title, text }) => {
    return (
        <div className="feature-box">
            <img src={image} alt={title} />
            <div className="f-text">
                <h4>{title}</h4>
                <p>{text}</p>
            </div>
        </div>
    );
};

const AboutImageSec2 = () => {
    return (
        <img
            className="sec2-image"
            src={sec2image}
            alt="section2 img"
        />
    );
};

const AboutFeatures = () => {
    return (
        <div className="about-features">
            <FeatureBox
                image={res_menuimage}
                title="Multi Cuisine"
                text="In the new era of technology we look in the future with certainty life."
            />
            <FeatureBox
                image={Aicon}
                title="Easy To Order"
                text="In the new era of technology we look in the future with certainty life."
            />
            <FeatureBox
                image={timerimage}
                title="Fast Delivery"
                text="In the new era of technology we look in the future with certainty life."
            />
        </div>
    );
};

const AboutSec2 = () => {
    return (
        <div className="sec2-about">
            <AboutImageSec2 />
            <AboutFeatures />
        </div>
    );
};

export default AboutSec2;